.startPage {
  text-align: center;
  font-style: normal;
}
.startPageLogo {
  height: 40vmin;
  pointer-events: none;
}
@media (prefers-reduced-motion: no-preference) {
  .startPageLogo {
      animation: App-logo-float infinite 3s ease-in-out;
  }
}
.startPageHeader {
  min-height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}
@keyframes App-logo-float {
  0% {
      transform: translateY(0);
  }
  50% {
      transform: translateY(10px);
  }
  100% {
      transform: translateY(0px);
  }
}
.button {
  font-family: CastlemarRegular, serif;
  appearance: none;
  font-size: 55px;
  padding: 0 22px 4px 22px;
  outline: none;
  border: 1px solid #4B4137;
  border-radius: 50%;
  color: #4B4137;
  cursor: pointer;
  background: #A67C00 none;
  transition: all 0.15s;
}
.button:hover,
.button:focus {
  border: 2px solid #A67C00;
}
.button:active {
  background-color: #A67C00;
}
